@import "sass/index";

$cardWidth: 255px;

.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, $cardWidth);
  gap: 10px;
  justify-content: start;

  & > * {
    width: $cardWidth;
  }

  @media (max-width: $mobileScreen) {
    display: block;
    position: relative;
    width: 100%;
  }
}

.link {
  text-decoration: none;
  color: inherit;
}

:export {
  #{'cardWidth'}: $cardWidth;
}
