@import "sass/index";

.item {
  background: use(--info-dark);
  border-radius: 20px;
  margin: 5px;
}
.element {
  padding: 15px 5px 10px;
}
.img_title_container {
  display: flex;
  align-items: center;
  margin: 0px 0px 10px 2.5px;
}
.icon {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  mix-blend-mode: difference;
}

.title {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1;
  color: use(--template-modal-title-color);
  margin: 0;
}
