@import "sass/index";

.arrow {
  $arrowSize: 32px;

  background: rgba(255, 255, 255, 0.05);
  color: use(--template-modal-title-color);
  position: absolute;
  padding: 0;
  right: 7px;
  top: -42px;

  border-radius: 50%;
  height: $arrowSize;
  width: $arrowSize;

  z-index: 1;

  &_left {
    margin-right: $arrowSize + 5px;
  }
  &_right {
  }

  &_disabled {
    display: none;
    pointer-events: none;
  }
}

.arrowsWrapper {
  position: relative;
}
