@import "sass/index";

.root {
  $textMargin: 10px;

  color: use(--template-modal-title-color);
  font-family: "Gilroy";
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin-left: $textMargin;
  width: calc(100% - var(--mini-banner-size) - (#{$textMargin} + 5px));

  display: flex;
  flex-direction: column;
  justify-content: space-around;

  max-height: 54px;

  & > * {
    color: use(--template-modal-title-color);

    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: start;
    overflow: hidden;
  }
}
.defaultText {
}
.valueTextWrapper {
}
.valueTextTitle {
  font-weight: 600;
}
.value {
  font-weight: 700;
  line-height: 15px;
  color: use(--template-appBar-menu-fill);
}
